<template>
  <div>
    <mrc_ess_table v-if="$route.name=='codeLogic'"
                   :headers='headers'
                   pageName='addCodeLogic'
                   :paginationOptions='options'
                   @turnPage='getlist'
                   :slotKey="['is_used','nameAndRemark']"
                   :isLoading='loading'
                   :items='desserts'>

      <template #item.nameAndRemark='{item}'>
        <div>
          <div>{{item.name}}</div>
          <div>{{item.remark}}</div>
        </div>
      </template>
      <!-- 状态 -->
      <template #item.is_used='{item}'>
        <v-btn rounded
               class="py-1"
               style="font-size: 11px;color: rgba(255, 255, 255, 0.95);font-family: Noto Sans CJK TC;font-style: normal;font-weight: normal;"
               color="#13a671"
               dark
               x-small
               v-if="item.is_used==1">正常</v-btn>
        <v-btn rounded
               class="py-1"
               color="#999999"
               style="font-size: 11px;color: rgba(255, 255, 255, 0.95);font-family: Noto Sans CJK TC;font-style: normal;font-weight: normal;"
               dark
               x-small
               v-if="item.is_used==0">暂停</v-btn>
      </template>

      <!-- 操作 -->
      <template v-slot:item.actions='{item}'>
        <menu-list :list='menulist'
                   @handleClick='handleClick($event,item)'>
          <template v-slot:showAction>
            <v-icon>mdi-dots-vertical</v-icon>
          </template>
        </menu-list>
      </template>
    </mrc_ess_table>
    <router-view v-else></router-view>
  </div>
</template>
 
<script>
import {
  getCodeLogicList, addCodeLogic
  , detailCodeLogic, updateCodeLogic, deleteCodeLogic
} from '@/service/api/codeLogic.js'
export default {
  components: {

  },
  props: {},
  data () {
    return {
      disabled: true,
      list: {},
      options: {
        pagesize: 5,//条数
        cpage: this.$store.state.cpage,  //页数
        totalCount: null, //总条数
        totalpage: null, //总页数
      },
      loading: false,
      menulist: [
        { name: this.$t('label_View'), show: true },
        { name: this.$t('label_Edit'), show: true },
        { name: this.$t('label_delete'), show: true },
      ],
      headers: [
        { text: 'Name&Method', align: 'start', value: 'nameAndRemark', sortable: false },
        { text: this.$t('label_Change_the_table'), value: 'change_table_uuid', sortable: false },
        { text: this.$t('状态'), value: 'is_used', sortable: false, align: 'center', width: '80' },
        { text: '', value: 'actions', sortable: false, align: 'end', width: '60' },
      ],
      desserts: [],
    };
  },
  beforeDestroy () {
    this.$store.commit('setCpage', 1)
  },
  computed: {},
  created () { },
  watch: {
    $route (v) {
      if (v.name == 'codeLogic') {
        this.getlist()
      }
    }
  },
  async mounted () {
    await this.getlist()
  },

  methods: {
    async getlist (obj) {
      this.loading = true
      if (this.desserts.length == 1 && this.options.cpage != 1) this.options.cpage--
      if (obj) {
        const { cpage, pagesize } = obj
        this.options = obj
      }
      const res = await getCodeLogicList({
        cpage: this.options.cpage, pagesize: this.options.pagesize
      })
      if (res.code == 200) {
        this.desserts = res.data
        this.options.totalCount = res.totalCount
        this.options.totalpage = res.totalpage
      }
      this.loading = false
    },
    handleClick ({ index }, item) {
      switch (index) {
        case 0:
          // this.$store.commit('set_subPage_name', item.param_name)
          this.$store.commit('set_iconstatus', true)  //行内进图标显示 
          this.$store.commit('set_uuid', item.uuid)  //
          this.$router.push({
            name: 'detailCodeLogic'
          })
          break
        case 1:
          // this.$store.commit('set_subPage_name', item.param_name)
          this.$store.commit('set_iconstatus', false)  //行内进图标显示 
          this.$store.commit('set_uuid', item.uuid)  //
          this.$router.push({
            name: 'detailCodeLogic'
          })
          break
        case 2:
          this.$myConfirm({ content: this.$t('message_Confirm_the_deletion') }).then(res => {
            deleteCodeLogic({ uuid: item.uuid }).then(res => {
              if (res.code === '200') {
                this.$Message({ text: this.$t('message_The_deletion_was_successful') })
                this.getlist()
              }
              else {
                this.$Message({ text: this.$t('删除失败'), color: rgb(0, 0, 0, 0.38) })
              }
            })
          })
          break
      }
    },
    // godetail (item) {
    //   this.$store.commit('set_iconstatus', true)  //行内进图标显示
    //   // this.$store.commit('set_subPage_name', item.param_name)
    //   this.$store.commit('set_uuid', item.uuid)  //
    //   this.$router.push({
    //     name: 'detailCodeLogic'
    //   })
    // },
    // addemailtemplate () {
    //   this.$store.commit('set_subPage_name', '')
    //   this.$store.commit('set_uuid', '')
    //   this.$store.commit('set_iconstatus', '')  //行内进图标显示
    //   this.$router.push({
    //     name: 'addCodeLogic'
    //   })
    // }
  },
};
</script>

<style scoped>
.cardtitle {
  position: relative;
}
.emailbtn {
  position: absolute;
  right: 70px;
  top: -50px;
}
</style>
