// 代码逻辑配置
import http from '../httpConfig'

const table = "mrbase_sys_org_levels_change_process"


// 列表
export const getCodeLogicList = (params = {}) => {
  params['~table~'] = table
  return new Promise((resolve, reject) => {
    http.post('/api/codelogic/list-codelogic/v1', params)
      .then(res => {
        resolve(res)
      })
      .catch(err => {
        reject(err)
      })
  })
}

